.footer{ 
    min-height: 250px;
    background-color: gray;
    display: flex;
}

.footerMobile{ 
    min-height: 250px;
    background-color: gray;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.column{
    width: 32%;
    padding: 1em;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center
}

.columnMobile{
    width: 75%;
    padding: 1em;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center
}

.logoFoot{
    max-width: 250px
}

.footCopy{
    margin: 1em;
    color: white;
    text-transform: uppercase
}

.applyButton{
    height: 75px;
    width: 200px;
    background-color: #f2f2f2;
    border-radius: 2em;
    box-shadow: 4px 0 8px 0 rgba(0, 0, 0, 0.2), 6px 0 20px 0 rgba(0, 0, 0, 0.19);
    display: flex;
    justify-content: center;
    align-items: center;
    color: rgb(173, 140, 196);
    text-decoration: none;
    font-weight: 500;
    text-transform: uppercase
}

.copy{
    color: white
}
