.splash{
    background-image: url("../../../src/Resources/splashHome.png");
    height: 100vh;
    width: 100vw;
    min-height: 350px;
    background-size: cover;
    background-position: bottom center;
    background-repeat: 'no-repeat';
    display: flex;
    flex-direction: column;
    justify-content: space-between; 
}

.copyContainer{
    display: flex;
    align-items: center;
    flex-direction: column;
    padding: 1em 0 2em 0;
    background-color: #f2f2f2
}

.copy{
    width: 65%;
    text-align: justify;
    padding: 1em;
    color: #444444;
    font-family: "Montserrat Alternates";
}

.headHome{
    font-size: 1.5em;
    padding: .5em;
    margin-top: 1em;
    color: #444444;
    font-family: "Montserrat Alternates";
    text-transform: capitalize;
}