.application{
    background-color: #f2f2f2;
}

.title{
    font-size: 1.5em;
    font-family: "Montserrat Alternates";
    background-color: #ece6fc;
    padding: .6em;
    color: gray;
    
}

.head{
    font-size: 1em;
    padding: 1.5em;
    text-transform: uppercase;
}

.label{
    padding: .5em;
}

.field{
    padding: .25em;
    margin: .25em
}

.container{
    display: flex;
    flex-wrap: wrap;
    align-items:center;
    flex-direction: row;
    margin: 0 10%;
    min-width: 375px
}

.note{
    font-size: .5em;
    text-transform: uppercase
}

.items{
    display: flex;
    flex-direction: row;
    margin: .25em 0
}

.radio{
    display: flex;
    align-items:center;
    justify-content: center
}
.radioButton{
   margin-top: 10px
}

.check{
    text-align: left;
    padding-left: 1em
}

.button{
    padding: 1em;
     background-color: #ece6fc;
     border-radius: 1em;
     margin: 1em;
     text-transform: uppercase;
     color: gray
}

.legal {
   display: flex;
   justify-content: flex-start;
   text-align: justify;
   margin: 1em 15%;
   font-size: .8em
}
